eriodo neolítico es el último denominado dentro de la Edad de Piedra. Este periodo presenta especial importancia ya que el hombre tiene un gran desarrollo cultural y agrícola, domestica algunos animales y por primera vez deja de ser nómada. Este es uno de los periodos con mayores cambios especialmente a nivel social, ya que se empiezan a formar los primeros asentamientos y con ello a construir lo que conocemos hoy en día como las grandes sociedades.")]),_c('p',{staticClass:"mb-4"},[_vm._v("El hombre aprende nuevas técnicas de siembra, cultiva arroz, maíz y trigo, lleva a cabo avances en la agricultura, recoge frutos y empieza la domesticación de animales destinado a la ganadería y se adapta el medio ambiente a sus necesidades; a pesar de esto con el crecimiento de la población, se empiezan a magnificar los problemas, se presentan las primeras guerras y se enfrentan muchos problemas de corte social. Continúa elaborando herramientas y elementos en piedra, madera y otros materiales.")]),_vm._m(21),_vm._m(22),_c('div',{staticClass:"h3"},[_vm._v("Edad antigua")]),_vm._m(23),_vm._m(24),_vm._m(25),_c('h4',{staticClass:"mb-4"},[_vm._v("Egipto ")]),_vm._m(26),_c('TabsC',{staticClass:"color-primario mb-5"},[_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Arquitectura"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5 mb-3 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/egipto-arquitectura.svg"),"alt":"Arquitectura egipcia"}})])]),_c('div',{staticClass:"col-md-1"}),_c('div',{staticClass:"col-md-6"},[_c('div',{staticClass:"h2 mb-4"},[_vm._v("Arquitectura")]),_c('p',[_vm._v("Los egipcios utilizaron principalmente la piedra en sus construcciones, por su fácil acceso y durabilidad. Sus edificaciones eran monumentales, con bases fuertes y muros gruesos.")]),_c('p',[_vm._v("Debido a la escasez de materiales, predominan en construcción el adobe (ladrillos de barro) y la piedra, fundamentalmente piedra caliza, también piedra arenisca y granito en cantidades enormes. Del imperio antiguo en adelante, la piedra fue reservada generalmente para tumbas y templos, mientras que los ladrillos fueron utilizados en viviendas, incluso en los palacios reales, fortalezas, muros de los recintos de los templos y de las ciudades, y para edificios subsidiarios en los complejos de los templos.")]),_c('p',[_vm._v("El clima de Egipto, afortunadamente seco y cálido preservó, como la aldea de Deir el-Medina, la ciudad de Kahun del imperio medio, o las fortalezas en Buhen y Mirgissa. Por otra parte, muchos templos y tumbas han perdurado porque fueron construidos de piedra, o asentadas en tierras altas, no afectadas por las inundaciones del río Nilo. Muchas antiguas ciudades egipcias han desaparecido porque estaban situadas cerca de las zonas cultivables del valle del Nilo, que eran inundadas periódicamente con el lodo del río y se elevaron lentamente durante milenios; o porque los ladrillos de adobe, con que se construyeron, fueron utilizados como fertilizante por los campesinos. Otros edificios son inaccesibles, ya que las nuevas construcciones fueron erigidas sobre las antiguas (Castro, 2014).")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Escritura"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5 mb-3 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/egipto-escritura.svg"),"alt":"Escritura egipcia"}})])]),_c('div',{staticClass:"col-md-1"}),_c('div',{staticClass:"col-md-6"},[_c('div',{staticClass:"h2 mb-4"},[_vm._v("Escritura")]),_c('p',[_vm._v("Los egipcios fueron grandes poseedores de conocimientos, durante siglos su escritura fue casi que indescifrable hasta que apareció la piedra Rosetta, la cual ayudó a descifrar algunos símbolos. Los egipcios no solo utilizaban los pictogramas, también tenían dentro de su escritura fonemas con un valor sonoro. Dichos jeroglíficos podían ser leídos en varias direcciones, de izquierda a derecha, de derecha a izquierda, de abajo a arriba y viceversa.")]),_c('p',[_vm._v("La piedra Rosetta es descubierta en 1779 cerca al brazo del Nilo, en Rosetta de donde toma su nombre, un oficial francés hace el descubrimiento y observan tres tipos de escritura diferente. La piedra es llevada a Londres en donde se empiezan a realizar estudios por varios eruditos, que terminan descifrando finalmente su contenido.")])])])])]),_c('h4',{staticClass:"mb-4"},[_vm._v("Grecia ")]),_vm._m(27),_c('p',[_vm._v("Este auge en Grecia se produjo en tres etapas: ")]),_vm._m(28),_c('TabsC',{staticClass:"color-primario mb-5"},[_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"La arquitectura griega"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5 mb-3 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/griega-arquitectura.svg"),"alt":"La arquitectura griega"}})])]),_c('div',{staticClass:"col-md-1"}),_c('div',{staticClass:"col-md-6"},[_c('div',{staticClass:"h2 mb-4"},[_vm._v("La arquitectura griega")]),_c('p',[_vm._v("Constituyó el punto de partida de lo que se iba a desarrollar posteriormente en el transcurso de más de dos mil años. Al principio utilizaron adobe o madera, que fue reemplazada por la piedra, sobre todo mármol. Trabajaron el espacio axial y ortogonal, las cubiertas horizontales, los espacios fueron diseñados para el hombre en forma inteligente, articulados al igual que un ser viviente, manteniendo perfecta simetría y proporción. Construyeron santuarios, que eran ciudades sagradas, en los cuales sacerdotes y pitonisas, buscaban la respuesta de los dioses a través del oráculo, se realizan juegos y pruebas deportivas, actividades musicales y teatrales. Las construcciones estaban rodeadas de columnas, idearon tres estilos de órdenes: dórico, jónico y corintio. En la cornisa, que es un componente horizontal, levantaron monstruosas gárgolas; otro elemento fundamental e importante a destacar son las obras de carácter lúdico como lo es el teatro, construidos en laderas de montañas, a gran escala y de forma semicircular. En cuanto a la vivienda urbana, se observa que tenían una planta rectangular con un patio en el centro, hacia el cual daban las estancias. Se puede afirmar, que se mantuvo el orden ortogonal arquitectónica y urbanísticamente, dando nacimiento al urbanismo, como pauta que determina cómo distribuir el espacio vital.")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Escultura y pintura"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5 mb-3 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/griega-escritura.svg"),"alt":"Escultura y pintura"}})])]),_c('div',{staticClass:"col-md-1"}),_c('div',{staticClass:"col-md-6"},[_c('div',{staticClass:"h2 mb-4"},[_vm._v("Escultura y pintura")]),_c('p',[_vm._v("Los romanos eran monoteístas y al conquistar Grecia, destruyeron muchas pinturas, pues los dominados eran politeístas. "),_c('strong',[_vm._v("La escultura se centró en la perfección del cuerpo humano, ")]),_vm._v("usualmente atletas desnudos que habían triunfado en los diversos juegos deportivos de la época. También elaboraron estatuas de dioses. Utilizaron la piedra caliza y el mármol; trabajaron el bronce, la madera, el oro, el marfil, piedras de colores y coronas de metal, entre otros.")]),_c('p',[_vm._v("Inicialmente, los cuerpos eran rígidos, a medida que evolucionó la escultura, le imprimieron movimiento, se hizo menos rígido el cuerpo, se arqueó y se evitó la verticalidad. Entonces recurrieron a la geometría para llegar a la perfección de las medidas corporales. Utilizaron nuevas técnicas de relieve y el retrato, posteriormente, se inclinaron por esculturas en posiciones más naturales, alejándose de la geometría y acercándose a la anatomía humana, alcanzando el realismo de la escultura helenística.")]),_c('p',[_vm._v("Los vasos de cerámica que eran utilizados para el uso doméstico se adornaban con figuras humanas de frente. Por medio de la técnica del escorzo, que es un recurso utilizado para dar la sensación de profundidad, ubicando las figuras en forma oblicua o perpendicular sobre la superficie que se pinta.")])])])])]),_c('h4',{staticClass:"mb-4"},[_vm._v("Roma ")]),_vm._m(29),_vm._m(30),_vm._m(31),_c('p',[_vm._v("Los romanos crearon el concepto de urbe, para dar nombre a la ciudad, pues las ciudades se erigían conforme a un diseño circular llamado orbis. En el centro residía el poder político, cruzado por dos ejes, que significaban la salida y el regreso a las conquistas. Utilizaron piedra, hormigón (verdadero aporte romano que permite la arquitectura abovedada) y ladrillo, a veces revestían con mármol. Construyeron el teatro (influenciado por el teatro griego), el anfiteatro (lugar donde se enfrentaban los gladiadores), el circo (donde se realizaban competencias de carros tirados por caballos y conducidos por aurigas), el acueducto (fueron sus inventores, y construyeron cloacas para conducir las aguas residuales), numerosos puentes y calzadas para que los ciudadanos pudieran comunicarse, termas o baños públicos para relajarse (calentaban el agua por medio de un horno subterráneo o hipocausto), la basílica (lugar de reunión para realizar transacciones comerciales y de administración de justicia), templos y viviendas unifamiliares llamadas domus (eran rectangulares, con un patio central y un jardín posterior). Los romanos revestían sus casas con pinturas murales al fresco y el suelo con mosaicos de mármol, también expresaron el arte por medio de retratos, paisajes y bodegones.")]),_vm._m(32),_vm._m(33),_c('div',{staticClass:"h3 mb-5"},[_vm._v("Edad media ")]),_c('h4',[_vm._v("El arte bizantino ")]),_vm._m(34),_vm._m(35),_vm._m(36),_c('h4',[_vm._v("El arte románico ")]),_vm._m(37),_vm._m(38),_vm._m(39),_c('h4',[_vm._v("El arte gótico")]),_vm._m(40),_vm._m(41),_vm._m(42),_vm._m(43),_vm._m(44),_c('div',{staticClass:"h3 mb-5"},[_vm._v("Edad moderna")]),_c('h4',[_vm._v("Renacimiento ")]),_vm._m(45),_vm._m(46),_vm._m(47),_c('p',{staticClass:"mb-5