:{"src":require("@/assets/curso/arte-bizantino.png"),"alt":"El arte bizantino"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-3"},[_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/arte-bizantino2.png"),"alt":"El arte bizantino"}})])]),_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("La pintura en este período representa el absoluto influjo de la religión en la vida social y política del Imperio Oriental, pero a comienzos del año 730, bajo la autoridad del emperador León III, surgió una crisis iconoclasta que duró hasta el siglo IX, cuando prohíbe la elaboración de imágenes religiosas en el Imperio, ya que consideró que dichas imágenes conducen a la idolatría, porque en la materia estaba lo maligno y por lo tanto no contribuían en propósito de seguir el camino a lo divino. Por esa razón los mosaicos fueron cubiertos con yeso, y dañaron muchas obras religiosas; dentro de los templos los motivos religiosos se reemplazaron por retratos del emperador, escenas de guerra, de carreras de caballos o de caza, vegetales y animales. Pero en 843, los ortodoxos derrotaron a los destructores, luego vino el cisma entre las iglesias de Constantinopla y Roma, entonces las imágenes volvieron a ser admitidas en los templos, pero con un valor sagrado, rindiendo culto a Dios por medio de ellas. Sin embargo, la libertad del artista se limitó, debiendo ajustarse a temas antiguos y conservando el estilo clásico. La parte superior de las construcciones religiosas, son una reproducción de los mundos celestiales, mientras que la tierra correspondía a la zona situada entre el suelo y el arranque de los arcos. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5 cajon color-acento-botones"},[_c('div',{staticClass:"col-lg-2 justify-content-center col-5 px-md-4 p-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/cruz-bizantina.svg"),"alt":"Cruz arte bizantino"}})])]),_c('div',{staticClass:"col-lg-9 p-lg-4 px-4"},[_c('p',[_c('strong',[_vm._v("Los temas más importantes son Cristo, la Virgen María, los santos, el juicio final y algunas fiestas cristianas. ")]),_vm._v("Se destacan los mosaicos con colores brillantes, revistieron los muros con teselas de vidrio, oro y plata, por el contrario, la pintura bizantina con sus iconos o imágenes sagradas trabajadas sobre madera con técnicas propias y utilizando colores opacos; elaboraron iconos, que eran imágenes sagradas pintadas sobre madera, recubiertas de una fina capa de yeso, en la que con un buril se dibujaba y se aplicaban colores al temple.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-3"},[_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("El arte románico surge en el siglo X y se extiende hasta el XII. En el siglo X, Otón es proclamado emperador del Sacro Imperio Romano Germánico. Este emperador junto con Carlomagno quien gobernó en el siglo IX, fijó las bases de la Europa feudal caracterizada por el feudo y la dependencia de los vasallos respecto a su señor. En el centro de la propiedad, que era una enorme extensión de tierra, estaba el castillo, la catedral o la abadía. El poder dependía de la cantidad de tierras que se poseyeran y en la cúspide social estaba el rey. En esa época la iglesia católica logró la unidad de Europa; pero la Iglesia estaba manejada por el poder civil, pues los obispos eran nobles a quienes los reyes u otros nobles les entregaban sus diócesis; en correspondencia, la institución del papado se fortaleció. Los monjes se dedicaron a realizar trabajos manuales, tareas agrícolas y artesanales, convirtiendo los monasterios benedictinos en centros económicos independientes y en focos culturales de primera importancia, ya que copiaban manuscritos antiguos, realizaron pinturas de miniaturas y redactaron crónicas históricas. Pero los monasterios también se implican en el entramado de la vida social y política de Europa, lo que conllevó inmoralidad y desorden. Por esta razón, en el año 910, nació la primera Abadía de Cluny, fundada por Guillermo el piadoso, "),_c('strong',[_vm._v("con la intención de regresar a una mayor espiritualidad, imponiendo el estilo románico ")]),_vm._v("en forma uniforme al construir conventos e iglesias y se vuelcan más en lo mundano que en lo divino, "),_c('strong',[_vm._v("poniendo la belleza al servicio de la fe, pues nada era ostentoso ni extremadamente rico para adorar a Dios. ")]),_vm._v("Pero frente a esta abadía, surge la orden de Císter, fruto de otra reforma, basada en la austeridad y la pobreza. Esta orden eliminó todo lo decorativo, suprimieron pinturas y esculturas, para ellos lo importante era lo estructural, dando origen al arte gótico.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/arte-romanico.png"),"alt":"El arte románico"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-3"},[_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/arte-romanico2.png"),"alt":"El arte románico"}})])]),_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("La iglesia promovió las peregrinaciones, las cuales incentivaron la vida cultural y artística, ya que fue necesario construir iglesias del estilo románico, en las que se realizaba el culto a las reliquias, como los cuerpos de los santos y sus objetos personales. Esto requería construcciones arquitectónicas en las que debía reposar el cuerpo del santo, y alojar a los peregrinos y fieles, así como una gran producción de imágenes en pinturas y esculturas, que aún son objeto de devoción. Así surgió la ruta de peregrinación de Santiago de