os campos hacia las regiones urbanas, en donde dominó la burguesía, clase fundamentalmente comerciante, que propendió por el sentido comunal, la defensa de los intereses de sus habitantes y la autonomía municipal. Por esa razón, al lado del poder religioso representado en las catedrales, se construyeron los ayuntamientos. Los artesanos y los artistas se organizaron en gremios que buscaban evitar la competencia y el monopolio. Los gremios se encargaron de realizar numerosas obras artísticas, siendo la ciudad un polo de desarrollo cultural, sobre todo por el surgimiento de las universidades, que desplazaron a los monasterios. En esa época, Santo Tomás de Aquino afirmaba que la razón precede a la fe, mientras que San Francisco de Asís anteponía la fe a la racionalidad. Ambas corrientes coincidían en que "),_c('strong',[_vm._v("el hombre había encontrado a Dios, ")]),_vm._v("pues él también habitaba en la tierra, y ya no era necesario buscarlo como se afirmaba en la época románica; "),_c('strong',[_vm._v("este pensamiento fue la base del estilo artístico gótico. ")]),_vm._v("Como la presencia divina se manifestaba a través de la luz, en la arquitectura gótica se utilizó el vidrio con tonos preferencialmente azules y rojos, y la piedra en forma de sillares, por cuanto lo estructural tuvo para la época enorme valor estético.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/arte-gotico.png"),"alt":"El arte gótico"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-3"},[_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/arte-gotico2.png"),"alt":"El arte gótico"}})])]),_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("La escultura también presentó un desarrollo importante; sin embargo, la pintura no se destacó sino hasta la primera mitad del siglo XIV, especialmente en Italia. También cambió el diseño de las catedrales más altas y de estructura nítida en el que las diferentes unidades espaciales perdieron independencia en pro de una mayor integración, procurando que entrara más luz, por eso los muros se construyen con grandes ventanales. El arco que caracterizó este período es el apuntado o también llamado ojival, siendo más esbelto, igualmente se construyeron canales para expulsar el agua lluvia a través de la boca de gárgolas. El arte gótico se caracterizó por ser vertical y abierto; alrededor de las ciudades se construyeron murallas, dos calles principales se cortaban en cruz, en el centro la iglesia y el ayuntamiento. Hacia el siglo XIV, la catedral deja de ser el principal centro y se construyeron casas consistoriales y palacios, iniciando una tendencia con muchos ornamentos, por lo que en el siglo XV se produce el gótico tardío (periodo final). Sin embargo, Italia siguió apegándose a lo clásico, por lo que a principios del siglo XV en Florencia se originó el renacimiento, mientras que el resto de Europa vivía la máxima expresión gótica. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5 cajon color-acento-botones"},[_c('div',{staticClass:"col-lg-2 justify-content-center col-5 px-md-3 p-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/catedral-de-milan.svg"),"alt":"Catedral de Milán"}})])]),_c('div',{staticClass:"col-lg-9 p-lg-4 px-4"},[_c('p',[_vm._v("La escultura gótica tuvo una función didáctica, ya que la mayoría de los fieles eran analfabetas, volvió al naturalismo y la armonía del hombre con la naturaleza. "),_c('strong',[_vm._v("Las imágenes se tornaron más reales, semejando movimiento y sentimientos, ")]),_vm._v("expresando el lado humano, apegándose a lo material y a lo individual como medio para llegar a Dios. En los templos la escultura tuvo mayor importancia, porque estaban en decadencia los frescos (