ercambio cultural entre oriente y occidente.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-10"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/renacimiento.png"),"alt":"Renacimiento"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_vm._v("A medida que más personas aprendían a leer y que los libros se imprimieron y llegaron a sitios geográficos diversos gracias al invento de la imprenta, creación de "),_c('strong',[_vm._v("Johannes Gutenberg, ")]),_vm._v("la capacidad de raciocinio, se generó una revolución intelectual en la búsqueda del conocimiento, pues la difusión de textos propendía por la renovación cultural y por la reinterpretación de los valores tradicionales greco-romanos. Los cambios se dieron gradualmente y afectaron a toda la sociedad en todos los aspectos. Esa revolución intelectual, condujo a que muchas personas empezaran a cuestionar el papel de la iglesia católica, situación que conllevó al surgimiento del protestantismo. Sin embargo, el hombre renacentista no era pagano, solamente tenía una práctica religiosa, diferente a épocas anteriores.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/mona-lisa.png"),"alt":"Retrato de Lisa Gherardini"}})])]),_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("El auge del Renacimiento fue entre 1490 y 1527, y en cuanto a la arquitectura Filippo di Ser Brunellesco Lapi, fue el más destacado, también fue escultor y orfebre. Utilizó sus grandes conocimientos en matemáticas para inventar la perspectiva cónica, utilizando también los elementos constructivos de la antigüedad. Sus principales obras son la cúpula de la catedral de Florencia, la basílica de San Lorenzo, la fachada del hospital de los Inocentes. Otro destacado representante del Renacimiento fue Michelangelo Buonarroti, quien era arquitecto, escultor y pintor, caracterizado por su perfeccionismo en la escultura y en la pintura (realizó la obra excepcional de la bóveda de la capilla Sixtina por imposición del Papa Julio II), elaboró esculturas muy importantes como La Piedad y El David. Así mismo, "),_c('strong',[_vm._v("Leonardo Da Vinci fue considerado el último hombre del Renacimiento, ")]),_vm._v("fue un gran polímata (persona que tiene conocimiento en diferentes disciplinas), por cuanto era pintor, anatomista, matemático, arquitecto, científico, escritor, paleontólogo, botánico, escultor, filósofo, ingeniero, músico, urbanista e inventor, tuvo creaciones relacionadas con el helicóptero, el submarino entre otras. Sus obras más representativas son La Gioconda y La Última Cena.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-3"},[_c('div',{staticClass:"col-lg-6"},[_c('p',[_vm._v("Este período abarcó el siglo XVII y principios del XVIII, y surgió en un período lleno de tensiones religiosas entre católicos y protestantes, y tuvo variaciones en cada país, conforme a si se trataba de un país regido por una monarquía absolutista o parlamentaria. Se extendió en Europa occidental y sus colonias americanas recientemente conquistadas. Nació en Italia en el período conocido como Seicento, que inicialmente se refería a un estilo artístico recargado y excesivo. El barroco expresaba una sensibilidad desengañada, crítica, compleja y pesimista. El término fue utilizado inicialmente para referirse a algo engañoso o recargado en forma despectiva, pero desde el siglo XIX, el término se utilizó para referirse no sólo al período sino a las manifestaciones artísticas que contradijeron los valores del clasicismo, mediante una negación simbólica y artística de lo clásico, por eso fue femenino, irracional y dionisíaco. ")])]),_c('div',{staticClass:"col-lg-1"}),_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/barroco.png"),"alt":"El Barroco"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-4 cajon color-acento-botones"},[_c('div',{staticClass:"col-lg-2 justify-content-center col-5 p-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/fuente.svg"),"alt":"Fuente"}})])]),_c('div',{staticClass:"col-lg-9 p-lg-4 px-4"},[_c('p',[_vm._v("El barroco enfatizó "),_c('strong',[_vm._v("lo cotidiano y efímero de la vida, ")]),_vm._v("lo cual produjo una vulgarización del imaginario religioso, consideró que lo nacional y religioso eran expresiones de poder político, por eso se elaboraron obras monumentales y recargadas que propendían por hacer resaltar el poder dominante, pero mediante engaños y artificios. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"mb-5"},[_vm._v("La pintura fue una expresión artística del barroco, se distinguen dos vertientes, una "),_c('strong',[_vm._v("naturalista ")]),_vm._v("y la otra es el "),_c('strong',[_vm._v("clasicismo, ")]),_vm._v("caracterizadas por el realismo superficial, los colores ricos, y el contraste entre las luces y las sombras. Exponentes de este período son Rembrandt, Rubens, Velásquez, Poussin, Vermeer y Caravaggio, quien realizaba una minuciosa observación anatómica con el uso del claroscuro que otorgaba dramatismo a su obra y que llegó a conocerse como tenebrismo. Caravaggio, plasmó vívidos momentos y escenas relacionadas con luchas violentas, muerte o tortura.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("En cuanto a la arquitectura, en el barroco tuvo formas más dinámicas, con amplio sentido teatral, así como decoración excesiva y exuberante. Utilizaron las formas cóncavas y convexas, los juegos ópticos con perspectiva y dieron importancia al urbanismo, pues los gobernantes deseaban transformar los espacios públicos en ciudades.")]),_c('p',[_vm._v("La escultura barroca estuvo vinculada con la religión y con la arquitectura porque decoraban los espacios públicos, los palacios y las locaciones religiosas. Eran dinámicas, expresivas y ornamentadas; expresaban pasiones y estaban recargadas de detalles. Esta se expresó en dos vertientes, una la gótica y otra la renacentista. En términos generales durante el período barroco se siguieron trabajando los temas de historia, mitología y religión, aunque preferían las obras llenas de dramatismo. La Iglesia Católica prefirió los temas como la inmaculada concepción, la virgen, la eucaristía, los santos, asuntos claves en la disputa del protestantismo.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/arquitectura-barroca.png"),"alt":"Retrato de Michelangelo Merisi da Caravaggio - Iglesia barroca"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/Pablo-Ruiz-Picasso.png"),"alt":"Pablo Ruiz Picasso"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5 cajon color-acento-botones"},[_c('div',{staticClass:"col-lg-2 justify-content-center col-5 px-lg-5 p-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/picasso.svg"),"alt":"Picasso"}})])]),_c('div',{staticClass:"col-lg-9 p-lg-4 px-4"},[_c('p',[_vm._v("El cubismo también se desarrolló en la escultura, con técnicas parecidas al "),_c('strong',[_vm._v("collage ")]),_vm._v("del cubismo sintético, que integró señales y fra