omo diluidas o mezcladas. Buscaron iluminación interior, exterior, iluminación natural filtrada o al aire libre con los reflejos sobre el agua, o la luz del sol a través de las hojas de los árboles. Ellos entendieron la ciudad como un paisaje. Sentían que un momento era irrepetible y que debía captarse de inmediato, igualmente comprendieron el efecto de la refracción de la luz sobre los objetos, seleccionando ángulos imprevistos en sus pinturas. Igualmente, los momentos desapercibidos u ocultos a los espectadores, fueron captados en las pinturas y el dibujo perfectamente delineado fue eliminado, coloreando directamente sobre el lienzo; recurriendo a dos técnicas, la primera mezclando un color sobre el otro, la segunda, colocando colores primarios uno al lado del otro para que, al ser observados, se cause la percepción de color secundario. Igualmente, al economizar detalles, realizaron pinceladas directas, a veces brochazos o trazos gruesos. Pintaron estaciones de tren, fiestas, parques, comidas, carreras de caballos, la vida bohemia entre otros temas.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/impresionismo.png"),"alt":"Impresionismo"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"mb-4"},[_vm._v("Fue un movimiento artístico con raíces filosóficas y estéticas de la antigüedad clásica, movimiento moralizante, social e intelectual, para adaptarse a la modernidad y reinterpretar sus formas, que surgió a mediados del siglo XVIII y se extendió hasta el XIX. Su furor se produjo debido al descubrimiento de Pompeya y Herculano, por lo que se realizaron numerosas expediciones para conocer las obras antiguas en sus lugares de origen. Además, "),_c('strong',[_vm._v("apareció la Ilustración o el Iluminismo, que defendía la razón y el conocimiento para fomentar el progreso; ")]),_vm._v("igualmente, tuvo lugar la revolución industrial qu